		//Testimonial Dekstop Swiper Slider

		let customerTestimonialSwiper = null;
		let codeHasRun =false;
		const initiateCustomerTestimonialSwiper = () => {
			customerTestimonialSwiper = new Swiper(".testimonial-content", {
				slidesPerView: 1,
				centeredSlides: false,
				spaceBetween: 16,
				pagination: {
					el: '.testimonial-container .swiper-pagination',
					clickable: true,
				},
				on: {
					init: function () {
						if (window.innerWidth >= 768) {
						if (codeHasRun) {
							return;
						  }
						const activeSlideIndex = this.activeIndex;

						const activeSlide = this.slides[activeSlideIndex];
						const dataValue = activeSlide.getAttribute('data-video-href');

						  const testimonialSliderBtn = document.getElementById("testimonial_slider_btn");

						  testimonialSliderBtn.href = dataValue;
						  codeHasRun = true;
						}
				  	},
					slideChange: function () {
						if (window.innerWidth >= 768) {

						const activeSlideIndex = this.activeIndex;

						const activeSlide = this.slides[activeSlideIndex];
						const dataValue = activeSlide.getAttribute('data-video-href');

						  const testimonialSliderBtn = document.getElementById("testimonial_slider_btn");

						  if(dataValue){
							  testimonialSliderBtn.href = dataValue;
						  }
						}
					  },
					}
			});
		};

		const handleCustomerTestimonialSwiper = () => {
			if (!customerTestimonialSwiper && document.querySelector(".testimonial-content")) {
				initiateCustomerTestimonialSwiper();
			}
		};

		window.addEventListener("load", handleCustomerTestimonialSwiper);

